import React from 'react'
import PropTypes from 'prop-types'

class Records extends React.Component {
  constructor(props){
    this.state = {
      data: []
    }
  }

  componentDidMount() {
    fetch()
      .then(data => {
        this.setState(data)
      })
  }

  render () {
    <div>
        <h4>Recent inserts</h4>
        {this.state.data.map(record => (
          <p>{record.procedureName}</p>
        ))}
    </div>
  }
}

export default Records;
